=\"settings['server.type'] === 'myopenhab'\">\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Password')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.password']\"\n\t\t\t\t\ttype=\"password\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t</label>\n\t\t</fieldset>\n\n\t\t<h3>{{t('openhab', 'Additional settings')}}</h3>\n\t\t<fieldset>\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Refresh interval')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.interval']\"\n\t\t\t\t\ttype=\"number\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t\t{{t('openhab', 'seconds')}}\n\t\t\t</label>\n\t\t</fieldset>\n\t</div>\n</template>\n\n<script>\nimport { loadSettings } from './utils/settings';\n\nconst SETTINGS = [\n\t'server.type',\n\t'server.url',\n\t'server.username',\n\t'server.password',\n\t'server.ignoreSSL',\n\t'server.interval',\n]\n\nexport default {\n\tname: 'Settings',\n\tdata: function() {\n\t\treturn {\n\t\t\tsettings: SETTINGS.reduce((obj, key) => ({ ...obj, [key]: '' }), {}),\n\t\t\tloading: false,\n\t\t\tsuccess: false,\n\t\t\terror: '',\n\t\t\ttimeout: null,\n\t\t}\n\t},\n\twatch: {\n\t\terror(error) {\n\t\t\tif (!error) return\n\t\t\tOC.Notification.showTemporary(error)\n\t\t},\n\t},\n\tasync created() {\n\t\ttry {\n\t\t\tthis.settings = await loadSettings(SETTINGS);\n\n\t\t\t// set initial value of server mode\n\t\t\tif (!this.settings['server.type']) {\n\t\t\t\tthis.settings['server.type'] = 'custom';\n\t\t\t\tthis.setValue('server.type', 'custom');\n\t\t\t}\n\t\t} catch (e) {\n\t\t\tthis.error = this.t('openhab', 'Failed to load settings')\n\t\t\tthrow e\n\t\t}\n\t},\n\tmethods: {\n\t\tonChange() {\n\t\t\tif (this.timeout) {\n\t\t\t\tclearTimeout(this.timeout)\n\t\t\t}\n\t\t\tthis.timeout = setTimeout(() => {\n\t\t\t\tthis.submit()\n\t\t\t}, 1000)\n\t\t},\n\t\tasync submit() {\n\t\t\tthis.loading = true\n\t\t\tfor (const setting in this.settings) {\n\t\t\t\tthis.setValue(setting, this.settings[setting])\n\t\t\t}\n\t\t\tthis.loading = false\n\t\t\tthis.success = true\n\t\t\tsetTimeout(() => { this.success = false }, 3000)\n\t\t},\n\t\tasync setValue(setting, value) {\n\t\t\ttry {\n\t\t\t\tawait new Promise((resolve, reject) => {\n\t\t\t\t\tOCP.AppConfig.setValue('openhab', setting, value, {\n\t\t\t\t\t\tsuccess: resolve,\n\t\t\t\t\t\terror: reject,\n\t\t\t\t\t})}\n\t\t\t\t)\n\t\t\t} catch (e) {\n\t\t\t\tthis.error = this.t('openhab', 'Failed to save settings')\n\t\t\t\tthrow e\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\t.d-flex {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t}\n\n\tinput[type='text'], input[type='password'] {\n\t\twidth: 250px;\n\t}\n\n\th3 {\n\t\tfont-weight: bold;\n\t}\n</style>\n"],"sourceRoot":""}]);
